.follow-us__item {
  &:not(:last-child) {
    margin-bottom: 24px;
  }
}
.follow-us__link {
  font-family: $second-font;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.36;
  letter-spacing: 0.2em;

  color: $heading-text-color;

  &:hover,
  &:focus {
    color: $accent-color;
  }
}
